import AnimationController from './Animation.controller';
import $ from 'jquery';
import svgjs from 'svg.js';
import fasterdom from 'fasterdom';

import { secondsToMS } from './../../../helper/global.helper';

const AnimationConfig = require( './../../../../config/styles/animation.json' ),
      animRotations   = {
	      top :    315,
	      bottom : 225
      };

let isAnimating  = false,
    isAnimatedIn = false;

/**
 * Rotate a bar with a given angle
 * @param {object} which - the bar to be transfomed
 * @param {int} angle - rotation angle
 * @param {bool} relative - whether the rotation should be performaed relative to current rotation
 * @param {int} fxDur - animation duration
 * @return {Promise} - to allow then based callbacks
 */
function rotatePromise ( which, angle, relative = false, fxDur = 500 ) {
	const self = this,
	      el   = self.bars[ which ];

	if ( typeof el === 'undefined' ) {
		return Promise.reject( which, fxDur );
	}

	if ( angle === 0 ) {
		return Promise.resolve( which );
	}

	return new Promise( ( resolve, reject ) => {
		self.$scope.one( 'stopAnimation.burger-svg', () => {
			el.stop();
			reject( which );
		} );

		el.animate( fxDur ).transform( { rotation : angle }, relative ).after( () => {
			resolve( which, fxDur );
		} );
	} );
}

/**
 * Translate a bar with a given amount of pixels
 * @param {object} which - the bar to be transfomed
 * @param {int} y - amount of y pixels to translate
 * @param {int} fxDur - animation duration
 * @return {Promise} - to allow then based callbacks
 */
function translatePromise ( which, y, fxDur = 500 ) {
	const self = this,
	      el   = self.bars[ which ];

	if ( typeof el === 'undefined' ) {
		return Promise.reject( which, fxDur );
	}

	if ( y === el.y() ) {
		return Promise.resolve( which );
	}

	return new Promise( ( resolve, reject ) => {
		self.$scope.one( 'stopAnimation.burger-svg', () => {
			el.stop();
			reject( which );
		} );

		el.animate( fxDur ).y( y ).after( () => {
			resolve( which, fxDur );
		} );
	} );
}

/**
 * Animate a click with a given duration
 * @param {int} fxDuration - transition duration
 * @returns {void}
 */
function animClickIn ( fxDuration ) {
	const self            = this,
	      animToY         = {
		      top :    self.bars.middle.attr( 'origY' ),
		      bottom : self.bars.middle.attr( 'origY' )
	      },
	      animToRotations = {
		      top :    animRotations.top,
		      bottom : animRotations.bottom
	      };

	if ( isAnimating ) {
		self.$scope.trigger( 'stopAnimation.burger-svg' );

		[ 'top', 'bottom' ].map( ( v ) => {
			const curRotation = self.bars[ v ].transform( 'rotation' );

			if ( curRotation !== 0 ) {
				animToRotations[ v ] = animToRotations[ v ] + ( curRotation < 0 ?
				                                                -360 - curRotation :
				                                                -curRotation );
			}
			return v;
		} );
	}

	isAnimating  = true;
	isAnimatedIn = true;

	Promise
			.all( [ 'top', 'bottom' ].map( ( v ) => {
				return translatePromise.call( self, v, animToY[ v ], fxDuration / 2 );
			} ) )
			.then( ( which ) => {
				self.bars.middle.hide();

				return Promise.all( which.map( ( v ) => {
					return rotatePromise.call( self, v, animToRotations[ v ], true, fxDuration / 2 );
				} ) );
			} )
			.then( () => {
				isAnimating = false;
			} )
			.catch( ( which ) => { } );
}

/**
 * Animate a click with a given duration
 * @param {int} fxDuration - transition duration
 * @returns {void}
 */
function animClickOut ( fxDuration ) {
	const self            = this,
	      animToY         = {
		      top :    self.bars.top.attr( 'origY' ),
		      bottom : self.bars.bottom.attr( 'origY' )
	      },
	      animToRotations = {
		      top :    -animRotations.top,
		      bottom : -animRotations.bottom
	      };

	if ( isAnimating ) {
		self.$scope.trigger( 'stopAnimation.burger-svg' );

		[ 'top', 'bottom' ].map( ( v ) => {
			const curRotation = self.bars[ v ].transform( 'rotation' );

			if ( curRotation === 0 ) {
				animToRotations[ v ] = 0;
			} else {
				animToRotations[ v ] = curRotation < 0 ?
				                       -360 - curRotation :
				                       -curRotation;
			}
			return v;
		} );
	}

	isAnimating = true;

	Promise
			.all( [ 'top', 'bottom' ].map( ( v ) => {
				return rotatePromise.call( self, v, animToRotations[ v ], true, fxDuration / 2 );
			} ) )
			.then( ( which ) => {
				self.bars.middle.show();

				return Promise.all( which.map( ( v ) => {
					return translatePromise.call( self, v, animToY[ v ], fxDuration / 2 );
				} ) );
			} )
			.then( ( which ) => {
				which.map( ( v ) => {
					self.bars[ v ].rotate( 0 );
					return v;
				} );
				isAnimating  = false;
				isAnimatedIn = false;
			} )
			.catch( ( which ) => { } );
}

/**
 * Add event handlers
 * @returns {void}
 */
function addHandler () {
	const self       = this,
	      fxDuration = secondsToMS( AnimationConfig.fx.duration.medium );

	self.$scope.parents( 'button' ).first().on( 'setActive.offScreen', () => {
		animClickIn.call( self, fxDuration );
	} );

	self.$scope.parents( 'button' ).first().on( 'setInactive.offScreen', () => {
		if ( isAnimatedIn ) {
			animClickOut.call( self, fxDuration );
		}
	} );

}

/**
 * Class representing an iframe instance.
 * Controls the state of the iframe.
 * Controls menu item behavior.
 * @extends { AbstractController }
 */
class BurgerMenuController extends AnimationController {

	constructor ( config ) {
		super( config );
	}

	/**
	 * Initialises the controller.
	 * Has to be called after ready event.
	 *
	 * @public
	 * @param { object } $root jQuery wrapped DOM node
	 * @return { void }        nothing
	 */
	init ( $root ) {
		const self = this;

		self.$scope = $( this.node );

		fasterdom.measureInstant( () => {
			self._w     = parseInt( self.$scope.attr( 'width' ), 10 );
			self._h     = parseInt( self.$scope.attr( 'height' ), 10 );
			self.id     = self.$scope.attr( 'id' );

			self.svg = svgjs( self.id ).size( self._w, self._h );

			self.group = self.svg.group();

			self.bars = {
				top :    self.svg.select( 'path[id*=_top], [id*=_top] path' ).first().addTo( self.svg ),
				middle : self.svg.select( 'path[id*=_middle], [id*=_middle] path' ).first().addTo( self.svg ),
				bottom : self.svg.select( 'path[id*=_down], [id*=_down] path' ).first().addTo( self.svg )
			};

			self.bars.top.attr( { origY : self.bars.top.y() } );
			self.bars.middle.attr( { origY : self.bars.middle.y() } );
			self.bars.bottom.attr( { origY : self.bars.bottom.y() } );

			addHandler.call( self );
		} );
	}

}

export default BurgerMenuController;



